<template>

<div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                          {{ApproveData[0].STATUS_NAME}}
                        </h5>
                        <button v-on:click="ClosePOP($event)" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>
                    </div>
                    <div class="border p-3">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"  v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>      

</template>

<script>
import DataService from '../../service/DataService'
export default{
   props:{
        ApproveData:{
            type:Array,
            required:true,
        }    
   },
    data(){
        return{
            FormSchema: [
                        {
                            layoutType: "single",
                            Data: [
                                    {
                                    astype: "SelectField",
                                    label: this.$t("Status"),
                                    name: "Status",
                                    value: this.ApproveData[0].STATUS_NAME==='Approve' ? "ASSET_RETURN_APPROVED" : 'ASSET_RETURN_REJECTED',
                                    placeholder: "",
                                    validationRules:{"required":false},
                                    disabled:'Disabled',
                                    config: {
                                        options: [                        
                                            { value: 'ASSET_RETURN_APPROVED', name: 'Approve' },
                                            { value: 'ASSET_RETURN_REJECTED', name: 'Reject' }
                                        ],
                                                                      
                                    },
                                    
                                },
                                {
                                    astype: "TextAreaField",
                                    label: this.$t('Remarks'),
                                    name: "Remarks",
                                    value: "",
                                    validationRules: { "required": false, max: 1000 },
                                    placeholder: ""  ,
                                    config: {
                                        rows:0
                                    }
                                },
                            ]
                        }
                          
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.onCancel
                }
            ],            
        }
    },
    created:function(){                 
       debugger;
    },
    methods:{
        
        onSubmit:function(formfields){
            debugger;           
            var vm=this; 
            vm.isLoading=true; 
            vm.ApproveData.forEach(item => {

             const ApproveRejectData ={
                    requestID:item.REQUEST_ID,
                    AssetAssignmentDetailId:item.ASSIGNMENT_DETAIL_ID,
                    statusCode:formfields.Status,
                    noOfItems:item.QTY,
                    Title:item.NAME,
                    comment:formfields.Remarks,
                    Userids:item.USER_ID,
                    PenalityAmount:item.PENALITY_AMOUNT,
                    ReturnPenalityStatusID:item.PENALITY_STATUS_ID,
                    GraceOverDueDays:item.GRACE_OVERDUE_DAYS,
                    
                } 
                debugger; 
        
                var Jsondata = JSON.stringify(item);
                    DataService.ApprovedReject(Jsondata).then(response => {
                    
                    if (response.data != null) {
                        if (response.data.msg) {                            
                            vm.ShowAlert(vm.$t(response.data.msg), "success", vm.$t('Alert'));
                            vm.isLoading=false; 
                            vm.$parent.GetAssetReturnRequestList();
                            vm.$parent.ClosePopUpApproveReject();
                        } else {
                            vm.ShowAlert(vm.$t("Unknown Error Occur"), "failure", true, vm.$t('Alert'));
                            vm.isLoading=false; 
                            vm.$parent.ClosePopUpApproveReject();
                        }
                    }
                    setTimeout(function() {
                        vm.CheckBoxBootstrap();
                    }, 100);
                    });
                });
            //     }
            // });
        },                
        onCancel:function(item){
            this.ClosePOP(item);
        },
        ClosePOP:function(item){
            
            this.$emit('ClosePopUpApproveReject',item);
        }
    }
}
</script>