                        "VISIBLE": true,
                        "DISPLAY_NAME": this.$t("ReturnDate"),
                        "DISPLAY_ORDER": 14,
                        settings:{
                                isInSlot : true
                            }                           
                    },                   
                                      
                                           
            ],
            widgets: [],
            ProductData: [],
            StatusData: [],
            ProductsData:[],
            selectedProducts:[],
            isUniqueNamesDisable:true,
            noRecord:true,
            TotalRecord:0,
            TotalRecords:0,
            TotalRecordes:0,
            CurrentPage: 1,
            CurrentPages: 1,
            CurrentPagees: 1,
            TotalPage: 0,
            TotalPages: 0,
            TotalPagees: 0,            
            CreatedBy:'',
            catIds:'',
            uIds:'',
            UserType:'',
            batchId:'',
            SortBy: '',
            SortExp: '',
            barcode:'',
            relatedInfoID:'',
            username:'',
            searchStatus:'',
            PenaltyOnStatus:'',
            PenaltyOnOverdue:'',  
            PageNumber: 1,
            PageNumbers: 1,
            PageNumberes: 1,
            PageSize: 10,
            PageSizes: 10,
            PageSizees: 10,
            ProductID:'', 
            ProductNames:'',
            type:'',
            activeTab: 1,
            tabs: [
                    { id: 1, title: 'Pending Check Out Request' },
                    { id: 2, title: 'Replace Request' },
                    { id: 3, title: 'Return Request' },
                    { id: 4, title: 'Reservation' }
            ],
            privilegeParams:'',
            totalDaysDue:0,
            navheading:'Pending Check Out Request',                         
            IscheckReject:false,
            IscheckReplace:false,
            IscheckRepair:false,            
            IscheckApproveReject:false,
            isLoading:false, 
            DataLoaded:0,     
            RejectData:[],
            ReplaceData:[],
            RepairData:[],
            ApproveData:[],
            Penaltyoverdue:0,
            StatusID:'',
            statusIDs:[],
            Headerstext:'Pending Request',
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [                      
                        {
                            astype: "TextField",
                            label: this.$t('Search'),
                            name: "Search",
                            value: "",
                            // disabled:true,
                            placeholder: "",
                            validationRules: { "required": false, max: 250 },
                            placeholder: ""                              
                        },
                        {
                            astype: "SelectField",
                            label: this.$t("Status"),
                            name: "Status",
                            value: "",
                            placeholder: "",
                            validationRules:{"required":false},
                            config: {
                                options: [{ value: "active", name: "Active" }, { value: "Approved", name: "Approved" },{ value: "Rejected", name: "Rejected" }],                               
                            },
                            
                        },
                        {
                            astype: "SelectField",
                            label: this.$t("PenaltyOnStatus"),
                            name: "PenaltyOnStatus",
                            value: "",
                            placeholder: "",
                            validationRules:{"required":false},
                            config: {                                
                                options: [{ value: "Yes", name: "Yes" }, { value: "No", name: "No" }],
                               
                            },
                            
                        }                     

                    ]
                },                
                
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-search pr-2'></i>"+this.$t('Search'),
                    onClick: this.SearchReturn
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-refresh pr-2'></i>"+this.$t('Clear'),
                    onClick: this.ClearReturn
                }
            ],

            FormSchemas: [
                {
                    layoutType: "four",
                    Data: [                     
                        {
                            astype: "SelectField",
                            label: this.$t("ProductName"),
                            name: "Status",
                            value: "",
