<template>

    <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                {{ $t("RepairRequest")}}: {{ RepairData.UNIQUE_NAME }}
                            </h5>
                            <button v-on:click="ClosePopUpRepair($event)" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>
                        </div>
                        <div class="border p-3">
                            <dynamic-form lang="en" :buttons="buttons" v-if="DataLoaded" :schema="FormSchema"  v-on:OnSubmit="onSubmit"></dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </template>
    
    <script>
    import DataService from '../../service/DataService'
    export default{
       props:{
        RepairData:{
            type:Array,
            required:true,
        }
       },
        data(){
            return{
                DataLoaded:0,
    
                FormSchema: [
                            {
                                layoutType: "single",
                                Data: [
                                        {
                                            astype: "SelectField",
                                            label: this.$t('Location'),
                                            name: "Location",
                                            value: "",
                                            placeholder: "",
                                            validationRules:{"required":true},
                                            config: {
                                                options: [],                                            
                                            },
                                        },
                                        {
                                            astype: "SelectField",
                                            label: this.$t("MaintenanceType"),
                                            name: "MaintenanceType",
                                            value: "",
                                            placeholder: "",
                                            validationRules:{"required":true},
                                                config: {
                                                    options: [
                                                        { value: 'Service', name: 'Service' },
                                                        { value: 'Repair', name: 'Repair' }
                                                    ],
                                                                                
                                                },                                    
                                        },
                                        {
                                            astype: "DateField",
                                            label: this.$t('ExpectedDate'),
                                            name: "ExpectedDate",
                                            value: '',
                                            placeholder: "10/12/2022 12:AM",
                                            validationRules:{"required":true},
                                            config: { format: "M/D/YYYY" }
                                        },
                                        {
                                            astype: "TextAreaField",
                                            label: this.$t('Remarks'),
                                            name: "Remarks",
                                            value: "",
                                            validationRules: { "required": false, max: 1000 },
                                            placeholder: "" ,
                                            
                                        },
                                ]
                            }
                            
                       ],
    
                buttons: [
                    {
                        type: "submit",
                        class: "btn btn-success",
                        text: "<i class='fa fa-save pr-2'></i>Save",
                    },
    
                    {
                        type: "button",
                        class: "btn btn-danger",
                        text: "<i class='fa fa-close pr-2'></i>Cancel",
                        onClick: this.onCancel
                    }
                ], 
                         
            }
        },
         created:async function(){
            
            var vm=this;         
            await vm.GetLocationByuserId();
            vm.DataLoaded=1;        
            
        },
        methods:{
            async GetLocationByuserId(){ 
                var vm=this;           
                var param=`reqForm=`;
                await DataService.GetLocationByCompanyId(param).then(response=>{                                
                    const LocationData = JSON.parse(response.data).data;
                        LocationData.forEach((item, index) => {
                             (vm.FormSchema)[0].Data[0].config.options.push({
                                 name: item.LOCATION_NAME,
                                 value: `${item.COMPANY_LOCATION_ID}`  
                             })                            
                        });               
                })  
               
            },
    
            onSubmit:function(formfields){                    
                var vm=this; 
                vm.isLoading=true; 
                debugger;
                const RepareData ={
                        Location:formfields.Location,
                        MaintenanceType  :formfields.MaintenanceType,
                        ExpectedDate:formfields.ExpectedDate,
                        comment:formfields.Remarks,                    
                    }  
               let postJSON = {
                        "Repair_Data": vm.RepairData,
                        "Json_Obj": RepareData,                    
                 };
               var Jsondata=JSON.stringify(postJSON);              
                DataService.SaveRepairRequest(Jsondata).then(response=>{                                            
                    if(response.data!=null){
                        if(response.data.length>0){                       
                            vm.ShowAlert(vm.$t(response.data +" successfully"), "success",vm.$t('Alert'));
                            vm.$parent.GetAssetReplacement();
                            vm.$parent.ClosePopUpRepair();                       
                        }
                        else{
                            vm.ShowAlert(vm.$t("Unknown Error Occur"), "failure", true, vm.$t('Alert'));
                            vm.$parent.ClosePopUpRepare();
                        }
                    }
                    setTimeout(function(){vm.CheckBoxBootstrap()}, 100);   
                });
           }, 
                    
            onCancel:function(item){
                this.ClosePopUpRepair(item);
            },
            ClosePopUpRepair:function(item){            
                this.$emit('ClosePopUpRepair',item);
            }
        }
    }
    </script>